table {
    width: 100%;
}

* { 
    text-align: center;
}

button {
    padding: 1em 3em;
}

.image {
    height: 10em;
}

.image-container {
    position: relative;
}
.results {
    height: 10em;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
}

.answer-indicator {
    width: 100%;
    position: absolute;
    top: 0;
    height:100%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: end;
        align-items: flex-end;
    -ms-flex-pack: center;
        justify-content: center;
}
.correct, .incorrect {
    width: 2em;
    height: 2em;
    opacity: 0;
    -webkit-animation: move .5s 1 ease-in-out;
            animation: move .5s 1 ease-in-out;
    background-size: contain;
    background-repeat: no-repeat;

}
.correct {
    background-image: url('../../images/success.png');
}

.incorrect {
    background-image: url('../../images/error.png');
}

@-webkit-keyframes move {
    from {
        opacity: 1;
    }
    to { 
        opacity: 0;
    }
}

@keyframes move {
    from {
        opacity: 1;
    }
    to { 
        opacity: 0;
    }
}

.description {
    color: grey;
    padding-top: 2em;
    width: 20em;
    margin: 0 auto;
}


